.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  font-size: 16px;
  color: white;
}

.header {
  background-color: white;
  width: 100%;
  color: #282c34;
  height: 50px;
  padding-left: 3%;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
}

.profil {
  width: 35px;
  height: 35px;
  border-radius: 30px;
  margin-right: 10px;
}

.content {
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 5%;
}

.subtitle {
  color: white;
  font-size: 16px;
  font-weight: 600;
}

.task {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.content_a {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.buttonAddClient {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-right: 5%;
}

.buttonAddTask {
  background-color: #008CBA;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-right: 5%;
}

.buttonShow {
  background-color: #f44336; 
  color: black;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
